const imgContainer = document.querySelector('.gallery');
imgContainer.innerHTML = images.map(imgTemplate).join('');

function imgTemplate (obj) {
    return `<li class="gallery-item">
      <a class="gallery-link" href="${obj.original}">
        <img
          class="gallery-image"
          src="${obj.preview}"
          data-source="${obj.original}"
          alt="${obj.description}"
          />
        </a>
      </li>`
}

imgContainer.addEventListener('click', e => {
  if (!e.target.classList.contains('gallery-image')) return;

  e.preventDefault();
  const imgBigUrl = e.target.dataset.source;
  const imgBigAlt = e.target.alt;
  
  const instance = basicLightbox.create(
    `<div class="modal"> <img src="${imgBigUrl}"  alt="${imgBigAlt}" width="1112" height="640"> </div>`,
    {
      closable: true,
      onShow: (instance) => {
        document.addEventListener('keydown', closeModal);
      },
      onClose: (instance) => {
        document.removeEventListener('keydown', closeModal);
      }
    }
  )

  function closeModal(e) {
    if (e.code === 'Escape') instance.close();   
  }
    
  instance.show()
});